.tooltip-container {
    position: relative;
    display: inline-block;
    cursor: auto;
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;

}

.tooltip-text {
    background-color: #464646;
    color: #c2c2c2;
    text-align: center;
    border-radius: 5px;
    padding: 7px;
    position: absolute;
    z-index: 1;
    left: 50%;
    transform: translateX(-50%);
    opacity: 0;
    transition: ease-in-out opacity 0.3s;
    white-space: nowrap;
    max-width: 80vw;
    overflow: hidden;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    text-overflow: ellipsis;
    visibility: hidden;
}

.tooltip-container:hover .tooltip-text {
    opacity: 1;
    width: auto;
    visibility: visible;
}

/* Positioning for top and bottom */
.tooltip-text.top {
    bottom: 100%;
    margin-bottom: 5px;
}

.tooltip-text.bottom {
    top: 100%;
    margin-top: 5px;
}
